        <div class="estimate">预计12:06到达</div>
      </div>
      <div class="settings div-flex">设置</div>
    </div>
  </div>

</template>

<script>
  import * as M from "../../../store/mutations-types";
  import PublishedScene from "../../../3d/js/PublishedScene";

  export default {
    name: "NavInfoBottom",
    data() {
      return {
        eventEndNavigating: {type: "end navigating"}
      }
    },
    methods: {
      endNavigating() {
        this.$store.commit(M.Is_Navigating, false);
        PublishedScene.navigatorTool().startSetEndPosition(false);
        PublishedScene.navigatorTool().clearPathAnimation();
        PublishedScene.scene().dispatchEvent(this.eventEndNavigating);
      }
    }
  }
</script>

