<template>
  <div v-title :data-title="pageTitle">
    <published-view ref="viewer"/>
    <widget-box class="widgets-box"/>

    <preview-compass-item class="compassIndicator"/>
    <preview-location-reset-item class="locationIndicator"/>
<!--    <floor-select-item class="floorSelect" :floors="floors"/>-->

  </div>
</template>

<script>
  import PublishedView from "../3d/PublishedView";
  import PreviewCompassItem from "../components/content/gadgetpreview/PreviewCompassItem";
  import PreviewLocationResetItem from "../components/content/gadgetpreview/PreviewLocationResetItem";
  import WidgetBox from "../components/content/gadgetpreview/WidgetBox";
  import SearchItem from "../components/content/gadgetpreview/SearchItem";
  import FloorSelectItem from "../components/content/gadgetpreview/FloorSelectItem";
  import MapScaleBtns from "../components/content/gadgetpreview/MapScaleBtns";
  import PreviewHead from "../components/content/gadgetpreview/PreviewHead";
  import BottomBox from "../components/content/gadgetpreview/BottomBox";
  import BuildingInfoBox from "../components/content/gadgetpreview/BuildingInfoBox";
  import PreviewReloadItem from "../components/content/gadgetpreview/PreviewReloadItem";
  import Preview3DToggleItem from "../components/content/gadgetpreview/Preview3DToggleItem";
  import PublishedScene from "../3d/js/PublishedScene";
  import Config from "../3d/js/configurable/Config";

  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);

  export default {
    name: "PreviewPage4pc",
    beforeCreate() {
      if (this.$route.query.fps != undefined)
        Config.ui.showFps = this.$route.query.fps == 1;
      Config.remote.updateByVue(this.$route);
      axios.defaults.baseURL = Config.remote.baseUrl;
      axios.defaults.headers.common['token'] = Config.remote.previewToken;
      // alert("preview, mapId: " + Config.remote.previewMapId);
    },
    mounted() {
      PublishedScene.setEnabeRotate(true);
    },
    watch:{
      $route(to, from) {
        // alert("to: " + to + " from: " + from);
        // console.log("route:", this.$route);
        Config.remote.updateByVue(this.$route);
        axios.defaults.headers.common['token'] = Config.remote.previewToken;
        this.$refs.viewer.loadFromRemote();
      }
    },
    data() {
      return {
        pageTitle: "pc端预览",
        floors: ["0", "1楼", "2楼"]
      };
    },
    methods: {
    },
    components: {
      PublishedView, PreviewCompassItem, PreviewLocationResetItem, WidgetBox,
      SearchItem, FloorSelectItem, MapScaleBtns, PreviewHead, BottomBox, BuildingInfoBox,
      PreviewReloadItem, Preview3DToggleItem
    }
  }
</script>

<style scoped>
  .compassIndicator {
    position: fixed;
    top: 20px;
    right: 30px;
  }
  .locationIndicator {
    position: fixed;
    top: 70px;
    right: 30px;
  }
  .floorSelect {
    position: fixed;
    top: 220px;
    right: 30px;
  }

  .widgets-box {
    position: absolute;
    bottom: 10px;
    left: 15px;
  }

</style>
