<template>
  <div class="top-box" :class="{'hide': $store.state.isNavigating || hidden}">
    <img src="../../../assets/img/mobile/StartMark.png" class="start-mark" >
    <img src="../../../assets/img/mobile/EndMark.png" class="end-mark" >
    <img src="../../../assets/img/mobile/SwitchIcon.png" class="switch-btn" @click="clickSwitchBtn">
    <input type="text" class="address-input input-start" v-model="startAddr" placeholder="请输入起点"
      @input="setMarkByName($event, true)">
    <img src="../../../assets/img/mobile/Close.png" class="btn-start-clear"
         @click="clearStartAddr" v-show="startAddr.trim().length > 0">
    <input type="text" class="address-input input-end" v-model="endAddr" placeholder="请输入终点"
           @input="setMarkByName($event, false)">
    <img src="../../../assets/img/mobile/Close.png" class="btn-end-clear"
         @click="clearEndAddr" v-show="endAddr.trim().length > 0">
    <div class="divider"></div>
    <div class="dot alpha03"></div>
    <div class="dot alpha06"></div>
    <div class="dot alpha1"></div>
  </div>
</template>

<script>
  import PublishedScene from "../../../3d/js/PublishedScene";
  export default {
    name: "TopBox",
    mounted() {
      PublishedScene.scene().addEventListener("top opacity change", (event) => {
        this.hidden = event.value == 0;
      });
      PublishedScene.scene().addEventListener("end navigating", (event) => {
        this.startAddr = "";
        this.endAddr = "";
      });
      PublishedScene.scene().addEventListener("building selected", (event) => {
        if (event.modelName.trim().length > 0)
          this.endAddr = event.modelName;
      });
    },
    data() {
      return {
        startAddr: "仁和天地",
        endAddr: "新世纪广场",
        hidden: false,
        debounceFun: null
      }
    },
    methods: {
      clickSwitchBtn() {
        // console.log("clickSwitchBtn");
        let temp = this.startAddr;
        this.startAddr = this.endAddr;
        this.endAddr = temp;
        PublishedScene.navigatorTool().switchMarker();
      },
      clearStartAddr() {
        this.startAddr = "";
      },
      clearEndAddr() {
        this.endAddr = "";
      },
      debounce(method, delay) {
        let timer = null;
        let _this = this;
        return function (arg) {
          let self = this;
          clearTimeout(timer);
          timer = setTimeout(function () {
            method.call(self, arg);
            _this.debounceFun = null;
          }, delay);
        }
      },
      setMarkByName(event, isStart) {
        let inputName = event.target.value;
        let setMarkImmediately = function (name) {
          PublishedScene.navigatorTool().setIconByName(name, isStart);
        };
        if (this.debounceFun == null) {
          this.debounceFun = this.debounce(setMarkImmediately, 500);
        }
        this.debounceFun(inputName);
      }
    }
  }
</script>

<style scoped>
  .top-box {
    position: fixed;
    top: 110px;
    left: 40px;
    right: 40px;
    height: 200px;
    background-color: white;
    box-shadow:0px 8px 34px 4px rgba(140,140,140,0.15);
    border-radius:20px;
    opacity: 1;
    transition: 0.3s;
  }
  .hide {
    opacity: 0;
  }
  .start-mark {
    position: absolute;
    top: 38px;
    left: 37px;
  }
  .end-mark {
    position: absolute;
    bottom: 33px;
    left: 38px;
  }
  .switch-btn {
    position: absolute;
    top: 57px;
    right: 39px;
  }
  .address-input {
    font-size: 28px;
    border: none;
    outline: none;
    width: calc(100% - 360px);
  }
  .input-start {
    position: absolute;
    top: 38px;
    left: 110px;
  }
  .input-end {
    position: absolute;
    bottom: 34px;
    left: 110px;
  }
  .btn-start-clear {
    position: absolute;
    top: 47px;
    right: 210px;
  }
  .btn-end-clear {
    position: absolute;
    bottom: 42px;
    right: 210px;
  }
  .divider {
    width: calc(100% - 288px);
    height: 1px;
    background-color: #EEE;
    position: absolute;
    top: 100px;
    left: 90px;
  }
  .dot {
    width:4px;
    height:4px;
    border-radius:50%;
    position: absolute;
    left: 50px;
  }
  .alpha03 {
    background:rgba(153,116,221,0.3);
    top: 93px
  }
  .alpha06 {
    background:rgba(153,116,221,0.6);
    top: 101px;
  }
  .alpha1 {
    background:rgba(153,116,221,1);
    top: 109px;
  }
</style>
