<template>
  <div class="map-scale-btns">
    <i class="scale-item iconfont iconicon-test" @click="zoomIn"></i>
    <i class="scale-item iconfont icon-" @click="zoomOut"></i>
  </div>
</template>

<script>
  import PublishedScene from "../../../3d/js/PublishedScene";

  export default {
    name: "MapScaleBtns",
    data() {
      return {
        scaleLst: [0.1, 0.3, 0.6, 1, 2.0, 4.0, 8.0]
      }
    },
    methods: {
      getNearest(arr, val, upper) {
        let target = {next: -1, index: -1, origin: val, upper: upper};
        for (let i = 0; i < arr.length; i++) {
          let v = arr[i];
          if (Math.abs(v - val) < 0.01) {
            target.index = i;
            if (upper) {
              if (i == arr.length - 1) {
                target.noBigger = true;
              } else {
                target.next = i + 1;
              }
            } else {
              if (i == 0) {
                target.noSmaller = true;
              } else {
                target.next = i - 1;
              }
            }
            return target;
          }

          if (v > val) {
            if (upper) {
              target.next = i;
            } else {
              if (i == 0) {
                target.noSmaller = true;
              } else {
                target.next = i - 1;
              }
            }
            return target;
          }
        }
        target.noBigger = true;
        return target;
      },
      zoomIn() {
        let curZoom = PublishedScene.currentCameraScale();
        let obj = this.getNearest(this.scaleLst, curZoom, false);
        if (obj.noSmaller) {
          return;
        }
        PublishedScene.startZoomCamera(this.scaleLst[obj.next]);
      },
      zoomOut() {
        let curZoom = PublishedScene.currentCameraScale();
        let obj = this.getNearest(this.scaleLst, curZoom, true);
        if (obj.noBigger) {
          return;
        }
        PublishedScene.startZoomCamera(this.scaleLst[obj.next]);
      }
    }
  }
</script>

<style scoped>
.map-scale-btns {
  width: 80px;
  height: 160px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 25px;
  color: black;
  background-color: #fff;

  box-shadow: 0px 1px 6px 0px rgba(0, 0, 0, 0.2);
}
  .scale-item {
    flex: 1;
    font-size: 40px;
  }
</style>
