<template>
  <div class="top-left-box">
    <div class="gadgetBaseCircle" ref="searchBtn" @mousedown="showInput">
      <input type="text" placeholder="搜索" class="searchTxt" @blur="hideInput" ref="searchInput"/>
      <i class="iconfont iconsousuo searchItem"></i>
    </div>
    <div class="btns-area" ref="btnsBox">
      <div class="btn-like" style="margin-right: 50px">
        <i class="iconfont iconweizhi" style="color: #2381F6; font-size: 35px;"/>
        <div class="btn-txt">我的位置</div>
      </div>
      <div class="btn-like" @mousedown="startSelectOnMap">
        <i class="iconfont icondituxuandian" style="color: #F34D45; font-size: 35px"/>
        <div class="btn-txt">地图选点</div>
      </div>
    </div>
  </div>
</template>

<script>
  import PublishedScene from "../../../3d/js/PublishedScene";
  import {Show_BottomBox, Show_BottomInfo} from "../../../store/mutations-types";
  import {hasClass} from "../../element/utils/dom";

  export default {
    name: "SearchItem",
    components: {},
    methods: {
      showInput() {
        this.$refs.searchBtn.classList.add('search-btn');
        this.$refs.searchInput.classList.add('searchTxtShow');
        this.$refs.btnsBox.classList.add('btns-area-show');
      },
      hideInput() {
        this.$refs.searchBtn.classList.remove('search-btn');
        this.$refs.searchInput.classList.remove('searchTxtShow');
        this.$refs.btnsBox.classList.remove('btns-area-show');
      },
      startSelectOnMap() {
        if (hasClass(this.$refs.searchBtn, "search-btn")) {
          this.hideInput();
          PublishedScene.navigatorTool().startSetEndPosition(true);
          this.$store.commit(Show_BottomBox, true);
          this.$store.commit(Show_BottomInfo, false);
        }
      }
    }
  }
</script>

<style scoped>
  .btn-txt {
    padding-left: 10px;
  }
  .top-left-box {
    width: calc(100% - 160px);
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .btns-area {
    padding-top: 25px;
    display: flex;
    justify-content: center;
    color: black;
    font-size: 35px;
    transition: 1s;
    opacity: 0;
  }
  .btns-area-show {
    opacity: 1;
  }
  .btn-like {
    border:1px solid rgba(235,235,235,1);
    box-shadow:0px 1px 6px 0px rgba(0, 0, 0, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 40px;
    padding: 20px;
    background-color: white;
  }

.searchItem {
  font-size: 44px;
  color: black;
  cursor: pointer;
}
  .search-btn {
    width: 100%;
  }

  .searchTxt {
    width: 0px;
    transition: 1s;
    font-size: 36px;
    border: none;
    outline: none;
  }
  .searchTxtShow {
    width: calc(100% - 80px);
    padding-left: 20px;
  }
</style>
