<template>
  <div class="floor-items">

    <div class="floors-lst" ref="floorLst">
      <div v-for="(item, index) in floors"
           class="hideCircle"
           :class="{active: index === currentIndex}"
           @mousedown="selectFloor(index)">
        <span class="floor-select-text">{{item}}</span>
      </div>
    </div>

    <div class="gadgetBaseCircle absolute" @mousedownmousedown="clickFloor">
      <span class="floor-select-text">{{currentLabel}}</span>
    </div>
  </div>

</template>

<script>
  export default {
    name: "FloorSelectItem",
    props: {
      floors: {
        type: Array,
        default() {
          return [];  // array of string
        }
      }
    },
    data() {
      return {
        dropdownShowing: false,
        currentLabel: "2楼",
        currentIndex: 2,
      }
    },
    methods: {
      selectFloor(index) {
        this.currentIndex = index;
        this.currentLabel = this.floors[index];
        this.dropdownShowing = false;
        this.hideFloorLst();
      },
      clickFloor() {
        this.dropdownShowing = !this.dropdownShowing;
        if (this.dropdownShowing) {
          this.showFloorLst();
        } else {
          this.hideFloorLst();
        }
      },
      showFloorLst() {
        this.$refs.floorLst.classList.add("floors-lst-show");
      },
      hideFloorLst() {
        this.$refs.floorLst.classList.remove("floors-lst-show");
      },
    }
  }
</script>

<style scoped>
  .floor-items {
    z-index: 3;
    width: 80px;
  }
.floor-select-text {
  font-size: 36px;
}
  .floors-lst {
    width: 80px;
    border-radius: 40px;
    background-color: #EDEDED;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow:0px 1px 6px 0px rgba(0, 0, 0, 0.2);
    transform-origin:40px 40px;
    transform:scaleY(0);
    transition: 0.5s;
    position: absolute;
  }
  .floors-lst-show {
    transform:scaleY(1);
  }

.hideCircle {
  width: 80px;
  height: 80px;
  border-radius: 40px;
  background-color: #EDEDED;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
  .active {
    background-color: #2381F6;
    color: white;
  }

  .absolute {
    position: absolute;
  }
</style>
