<template>
  <div class="content" ref="contentDiv">
    <p style="text-align: center" ref="progressText">0%</p>
    <div class="bar" ref="bar" @mousedown="clickBar($event)" id="sBar">
      <div class="progress" ref="progress"></div>
      <div class="dot" ref="dot" @mousedown="mousedownDot($event)" id="sDot"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Slider",
    props: {
      min: {
        type: Number,
        default: 0
      },
      max: {
        type: Number,
        default: 100
      },
      prefix: {
        type: String,
        default: ""
      },
      suffix: {
        type: String,
        default: ""
      },
    },
    data() {
      return {
        rest: 100,
        slideVal: 0
      }
    },
    methods: {
      updateSlideVal(bili, noEmit) {
        if (noEmit)
          this.rest = this.$refs.bar.offsetWidth - this.$refs.dot.offsetWidth
        this.slideVal = Math.ceil((this.max - this.min) * bili + this.min );
        this.$refs.dot.style.left = this.rest*bili + 'px'
        this.$refs.progressText.innerText = this.prefix + this.slideVal + this.suffix
        this.$refs.progress.style.width = this.$refs.bar.offsetWidth*bili + 'px';
        if (!noEmit)
          this.$emit('sliding', this.slideVal);
      },
      mousedownDot(event) {
        this.rest = this.$refs.bar.offsetWidth - this.$refs.dot.offsetWidth
        /*
        * offsetLeft 获取的是相对于父对象的左边距, 返回的是数值， 没有单位
        */
        let dotL = this.$refs.dot.offsetLeft;
        /*
        * clientX 事件属性返回当事件被触发时鼠标指针向对于浏览器页面（或客户区）的水平坐标。
        */
        let mouseX = event.clientX //鼠标按下的位置
        let _this = this;
        window.onmousemove = function (ev) {
          let e = ev || window.event
          // 浏览器当前位置减去鼠标按下的位置
          let moveL = e.clientX - mouseX //鼠标移动的距离

          // 保存newL是必要的
          let newL = dotL + moveL //left值
          // 判断最大值和最小值
          if (newL < 0) {
            newL = 0
          }
          if (newL >= _this.rest) {
            newL = _this.rest
          }

          // 计算比例
          let bili = newL / _this.rest
          _this.updateSlideVal(bili);

          return false //取消默认事件
        }
        window.onmouseup = function () {
          window.onmousemove = null //解绑移动事件
          window.onmouseup = null
          return false
        }
        event.stopPropagation();
        return false
      },
      contentOffsetLeft() {
        let dom = this.$refs.contentDiv;
        let left = dom.offsetLeft;
        while (dom.offsetParent) {
          dom = dom.offsetParent;
          left += dom.offsetLeft;
        }
        return left;
      },
      clickBar(ev) {
        this.rest = this.$refs.bar.offsetWidth - this.$refs.dot.offsetWidth
        // console.log("clicked slider bar")
        let left = ev.clientX - this.contentOffsetLeft() - this.$refs.dot.offsetWidth / 2
        if (left < 0) {
          left = 0
        }
        if (left >= this.rest) {
          left = this.rest
        }

        let bili = left / this.rest
        this.updateSlideVal(bili);
        return false
      }
    },
    mounted() {
      // offsetWidth 获取当前节点的宽度 （width + border + padding）
      // 总长度减去原点覆盖的部分
    }
  }
</script>

<style scoped>
  .content {
    min-width: 50px;
    height: 54px;
    position: relative;
  }

  .bar {
    width: 100%;
    height: 6px;
    border-radius: 10px;
    background: #e4e7ed;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    cursor: pointer;
  }
  .progress {
    width: 0;
    height: 6px;
    border-radius: 10px;
    background: #409eff;
  }
  .dot {
    width: 10px;
    height: 10px;
    background: #fff;
    border: 1px solid #409eff;
    position: absolute;
    bottom: 0;
    top: 0;
    margin: auto 0;
    border-radius: 50%;
    cursor: pointer;
  }
</style>
