<template>
  <div class="gadgetBaseCircle" :class="{'active': active}">
    <i class="locationReset iconfont icon3d" @click="toggleView3d"></i>
  </div>
</template>

<script>
  import PublishedScene from "../../../3d/js/PublishedScene";

  export default {
    name: "LocationResetItem",
    methods: {
      toggleView3d() {
        this.active = !this.active;
        PublishedScene.changePhiCamera(!this.active);
      }
    },
    data() {
      return {
        active: true
      }
    }
  }
</script>

<style scoped>
.locationReset {
  font-family: "iconfont" !important;
  font-size: 45px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding-top: 1px;
}

  .active {
    background-color: #2381F6;
    color: white;
  }
</style>
