<template>
  <ul class="drawer-bottom" :class="{show: this.show}" id="lstBox">
    <li v-for="(item, index) in maps"
         class="map-title"
         :class="{active: index === currentIndex}"
         @touchstart="onChangeMap($event, index)">
      {{item.name}}
    </li>
  </ul>
</template>

<script>
  import PublishedScene from "../../../3d/js/PublishedScene";
  import {default as M, Show_CompassItem} from "../../../store/mutations-types";

  export default {
    name: "MapSelectDrawer",
    props: {
      maps: {
        type: Array,
        default() {
          return []; //array of object
        }
      }
    },
    data() {
      return {
        currentMap: "",
        currentIndex: -1,
        show: false,

        is2D: true
      }
    },
    mounted() {
      PublishedScene.dom().addEventListener('touchstart', () => {
        this.show = false;
      });
    },
    methods: {
      loadJson(index) {
        this.currentIndex = index;
        this.currentMap = this.maps[index].name;
        let uri = 'assets_map/' + this.currentMap;
        let xmlhttp = new XMLHttpRequest();
        let _this = this;
        xmlhttp.onreadystatechange = function () {
          if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
            PublishedScene.deserialize(xmlhttp.responseText);
            _this.show = false;
          }
        };
        xmlhttp.open("GET", uri, true);
        xmlhttp.send();
      },
      onChangeMap(event, index) {
        let obj = this.maps[index];
        if (obj.command != undefined) {
          this.show = false;
          if (obj.command == "toggle 2d") {
            this.is2D = !this.is2D;
            PublishedScene.changePhiCamera(this.is2D);
          } else if (obj.command == "toggle compass") {
            let enableRotate = !this.$store.state.showCompassItem;
            this.$store.commit(Show_CompassItem, enableRotate);
            PublishedScene.setEnabeRotate(enableRotate);
          }
          return;
        }
        // console.log(event)
        event.target.addEventListener('touchmove', touchmove);
        event.target.addEventListener('touchend', touchend);

        let _this = this;
        function touchend() {
          _this.loadJson(index);
        }
        function touchmove() {
          event.target.removeEventListener('touchmove', touchmove);
          event.target.removeEventListener('touchend', touchend);
        }
      },
      showing(val) {
        this.show = val;
      }
    }
  }
</script>

<style scoped>
  .drawer-bottom {
    position: absolute;
    z-index: 99;
    left: 0;
    right: 0;
    bottom: -90%;
    max-height: calc(100% - 400px);
    overflow-x: hidden;
    overflow-y: auto;
    transition: 0.3s;
    visibility: hidden;
  }
  .show {
    bottom: 0;
    visibility: visible;
  }
.map-title {
  list-style: none;
  font-size: 44px;
  color: black;

  display: flex;
  justify-content: center;
  background-color: white;
  line-height: 1.5;
  border-bottom: solid 1px #999;
}
  .active {
    color: #55aaff;
    font-weight: bold;
  }
</style>
