<template>
  <div class="addr-info-box" :class="{show: $store.state.showBottomInfo}">
    <div class="text-box">
      <div class="addr-name">{{addrName}}</div>
      <div class="addr-desc">
        {{distance}}
        <div class="divider"/>
        {{addrDetail}}
      </div>
    </div>
    <div class="btn-box" @mousedown="clickGoHere">
      <div><i class="iconfont icondaohang" style="font-size: 62px"/></div>
      <div style="padding-top: 20px">去这里</div>
    </div>
  </div>
</template>

<script>
  import PublishedScene from "../../../3d/js/PublishedScene";
  import {Show_BottomInfo, Show_BottomBox} from "../../../store/mutations-types";

  export default {
    name: "BuildingInfoBox",
    data() {
      return {
        addrName: "仁和天地",
        distance: "距您487米",
        addrDetail: "四川省成都市高新区天府一街234 四川省成都市高新区天府一街234 四川省成都市高新区天府一街234"
      }
    },
    mounted() {
      PublishedScene.scene().addEventListener("building selected", event => {
        if (event.modelName.trim().length > 0) {
          this.addrName = event.modelName;
          this.$store.commit(Show_BottomInfo, true);
        } else {
          this.$store.commit(Show_BottomInfo, false);
        }
      });
    },
    methods: {
      clickGoHere() {
        PublishedScene.navigatorTool().setDistination();
        this.$store.commit(Show_BottomInfo, false);
        this.$store.commit(Show_BottomBox, true);
      }
    }
  }
</script>

<style scoped>
  .addr-info-box {
    position: absolute;
    bottom: -340px;
    left: 30px;
    right: 30px;
    height: 223px;
    background-color: white;
    box-shadow:0px 1px 6px 0px rgba(0, 0, 0, 0.2);
    border-radius:20px;
    display: flex;
    transition: 0.5s;
  }
  .show {
    bottom: 40px;
  }
  .btn-box {
    flex: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 42px;
    color: #2381F6;
  }
  .text-box {
    width: calc(100% - 223px);
    height: 100%;
    border-right: 1px solid #bfbfbf;
    padding-left: 15px;
  }
  .addr-name {
    color: #333;
    font-size: 60px;
    padding-top: 40px;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
  }
  .addr-desc {
    font-size: 40px;
    color: #999999;
    width: 100%;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
    padding: 20px 0;
  }
  .divider {
    margin: 0 20px;
    width: 1px;
    height: 0.8em;
    background-color: silver;
    display: inline-block;
  }
</style>
