<template>
  <div v-title :data-title="pageTitle">
    <published-view/>
    <preview-head class="headPos" v-show="!$store.state.isNavigating"/>
    <nav-detail-top />
    <top-box />
    <img src="../assets/img/mobile/ResetIcon.png" class="btn-reset" :class="{'hide': $store.state.isNavigating}"
      @click="resetCamera">
    <preview-compass-item class="compass-item" :class="{hide: !$store.state.showCompassItem}"/>

    <widget-box class="widgets-box"/>

    <building-bottom/>
    <nav-info-bottom v-show="$store.state.isNavigating"/>

    <map-select-drawer :maps="maps" ref="mapsDrawer"/>
  </div>
</template>

<script>
  import PublishedView from "../3d/PublishedView";
  import TopBox from "../components/content/preview2/TopBox";
  import WidgetBox from "../components/content/gadgetpreview/WidgetBox";
  import PreviewHead from "../components/content/gadgetpreview/PreviewHead";
  import BuildingBottom from "../components/content/preview2/BuildingBottom";
  import NavInfoBottom from "../components/content/preview2/NavInfoBottom";
  import NavDetailTop from "../components/content/preview2/NavDetailTop";
  import PublishedScene from "../3d/js/PublishedScene";
  import MapSelectDrawer from "../components/content/preview2/MapSelectDrawer";
  import PreviewCompassItem from "../components/content/gadgetpreview/PreviewCompassItem";
  import * as M from "../store/mutations-types";
  import Config from "../3d/js/configurable/Config";

  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);

  export default {
    name: "PreviewV2",
    beforeCreate() {
      Config.remote.previewToken = this.$route.query.token;
      Config.remote.previewMapId = this.$route.query.mapId;
      Config.remote.heatmap = this.$route.query.heatmap;
      Config.remote.track = this.$route.query.track;
      Config.remote.version = this.$route.query.version;
      axios.defaults.baseURL = Config.remote.baseUrl;
    },
    mounted() {
      // 阻止双击放大
      var lastTouchEnd = 0;
      document.addEventListener('touchstart', function(event) {
        if (event.touches.length > 1) {
          event.preventDefault();
        }
      });
      document.addEventListener('touchend', function(event) {
        var now = (new Date()).getTime();
        if (now - lastTouchEnd <= 300) {
          event.preventDefault();
        }
        lastTouchEnd = now;
      }, false);

      // 阻止双指放大
      document.addEventListener('gesturestart', function(event) {
        event.preventDefault();
      });

      PublishedScene.scene().addEventListener("open map list", () => {
        this.$refs.mapsDrawer.showing(true);
        this.$store.commit(M.Show_BottomInfo, false);
      });
    },
    data() {
      return {
        pageTitle: "手机端预览",
        maps: [
          {name:"指北针", command:"toggle compass"}, {name:"2d/3d切换", command:"toggle 2d"},
            {name:"东客站负一层.json"}, {name:"东客站负二层.json"},
          {name:"华西负一层.json"}, {name:"华西负二层.json"}, {name:"华西负三层.json"},
          {name:"地下车库智能化平面图.json"},
          {name:"6-2.json"}, {name:"6-3.json"}, {name:"6-4.json"}, {name:"6-5.json"},
          {name:"9-1.json"}, {name:"9-2.json"}, {name:"9-3.json"}, {name:"9-4.json"},
          {name:"map21.json"}, {name:"map01.json"}, {name:"mapOffice.json"}, {name:"mapAlign.json"}]
      }
    },
    components: {
      NavDetailTop,
      BuildingBottom, NavInfoBottom,
      PublishedView, TopBox, WidgetBox, PreviewHead,
      MapSelectDrawer, PreviewCompassItem
    },
    methods: {
      resetCamera() {
        PublishedScene.startResetingCamera();
      }
    }
  }
</script>

<style scoped>
  .headPos {
    position: fixed;
    top: 0;
  }

  .widgets-box {
    position: absolute;
    bottom: 41%;
    left: 15px;
  }

  .btn-reset {
    position: absolute;
    right: 45px;
    bottom: 301px;
    transition: 0.3s;
    opacity: 1;
  }
  .compass-item {
    position: absolute;
    right: 45px;
    bottom: 390px;
  }
  .hide {
    opacity: 0;
  }

</style>
