<template>
  <div>
<!--    <button @click="onclickPathTracking">轨迹演示</button>-->
    <button @click="onclickPathTracking(true)" ref="trackBtn">轨迹回放</button>
    <button @click="onclickHeatmap" ref="heatmapBtn" style="display: none">(隐藏/显示)热力图</button>
    <slider ref="sliderDom" v-show="showHeatmapSlider" style="width: 150px" @sliding="slidingHeatmapMax"
    :min="minHeat" :max="maxHeat" prefix="最大热力："/>
    <br>
    <select id="mapLst" @change="onChangeMap($event)" style="display: block">
    </select>

    <div id="bdtts_div_id">
      <audio id="tts_autio_id">
        <source id="tts_source_id" src="https://tts.baidu.com/text2audio?lan=zh&ie=UTF-8&spd=6&text=你好" type="audio/mpeg">
        <embed id="tts_embed_id" height="0" width="0" src="">
      </audio>
    </div>

<!--    <div class="toggleNavBtn" @click="toggleNavState()" :class="{'navigatingInfo': navigating}">-->
<!--      {{navBtnTxt}}-->
<!--    </div>-->
  </div>
</template>

<script>
  import PublishedScene from "../../../3d/js/PublishedScene";
  import Config from "../../../3d/js/configurable/Config";
  import Slider from "../../common/Slider";

  export default {
    name: "WidgetBox",
    components: {Slider},
    data() {
      return {
        mapLst: [
          "东客站负一层.json", "东客站负二层.json",
          "华西负一层.json", "华西负二层.json", "华西负三层.json", "地下车库智能化平面图.json",
          "6-2.json", "6-3.json", "6-4.json", "6-5.json",
          "9-1.json", "9-2.json", "9-3.json", "9-4.json",
            "map21.json", "map01.json", "mapOffice.json", "mapAlign.json"],
        defaultSelectIndex: 1,
        remainMeters: 0,
        navigating: false,
        navBtnTxt: "开始导航",
        showHeatmapSlider: false,
        minHeat: 1,
        maxHeat: 277
      }
    },
    mounted() {
      // this.showHeatmapSlider = true;
      // let _this0 = this;
      // setTimeout(() => {
      //   _this0.$refs.sliderDom.updateSlideVal(0.5, true);
      // }, 2000)
      if (Config.remote.track == undefined)
        this.$refs.trackBtn.style.display = 'none';
      if (Config.remote.heatmap == undefined) {
        this.$refs.heatmapBtn.style.display = 'none';
      }

      let selectCombo = document.getElementById('mapLst');
      for (let i = 0; i < this.mapLst.length; i++) {
        let optionNode = document.createElement("option");
        optionNode.value = i;
        if (i == this.defaultSelectIndex) {
          optionNode.selected = true;
        }
        let txtNode = document.createTextNode(this.mapLst[i]);
        optionNode.appendChild(txtNode);
        selectCombo.appendChild(optionNode);
      }
      // if (!this.$route.query.fromEditor) {
      //   this.loadJson(this.mapLst[this.defaultSelectIndex]);
      // }

      let _this = this;
      PublishedScene.scene().addEventListener('range heatmap', (event) => {
        _this.showHeatmapSlider = true;
        _this.minHeat = event.obj.min;
        _this.maxHeat = event.obj.max;
        setTimeout(()=>{
          _this.$refs.sliderDom.updateSlideVal(event.obj.bili, true);
        }, 500)
      });
      PublishedScene.scene().addEventListener('remain meters', onRemainMetersChange);
      function onRemainMetersChange(event) {
        _this.remainMeters = parseInt(event.meters);
        if (_this.remainMeters <= 70) {
          _this.playAudioByText("到达目的地，导航结束");
          PublishedScene.endNavigating();
          _this.navigating = false;
          _this.navBtnTxt = "开始导航";
        }
      }
    },
    methods: {
      slidingHeatmapMax(val) {
        // console.log("slidingHeatmapMax: ", val)
        if (!isNaN(val))
          PublishedScene.updateMax4Heatmap(val);
      },
      loadJson(fileName) {
        let uri = 'assets_map/' + fileName;
        let xmlhttp = new XMLHttpRequest();
        xmlhttp.onreadystatechange = function () {
          if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
            PublishedScene.deserialize(xmlhttp.responseText);
            // console.log(xmlhttp.responseText);
          }
        };
        xmlhttp.open("GET", uri, true);
        xmlhttp.send();
      },
      onChangeMap(event) {
        this.loadJson(this.mapLst[event.target.value]);
      },
      toggleNavState() {
        this.navigating = !this.navigating;
        this.playAudioByText(this.navBtnTxt);
        this.navBtnTxt = this.navigating ? "取消导航" : "开始导航";
        PublishedScene.toggleNavigatingSwitch(this.navigating);
      },
      playAudioByText(text) {
        var ttsDiv = document.getElementById('bdtts_div_id');
        var ttsAudio = document.getElementById('tts_autio_id');

        // 这样就可实现播放内容的替换了
        ttsDiv.removeChild(ttsAudio);
        var au1 = '<audio id="tts_autio_id" autoplay="autoplay">';
        var sss = '<source id="tts_source_id" src="https://tts.baidu.com/text2audio?lan=zh&ie=UTF-8&spd=5&text='+text+'" type="audio/mpeg">';
        var eee = '<embed id="tts_embed_id" height="0" width="0" src="">';
        var au2 = '</audio>';
        ttsDiv.innerHTML = au1 + sss + eee + au2;

        ttsAudio = document.getElementById('tts_autio_id');

        ttsAudio.play();
      },
      onclickPathTracking(replay) {
        PublishedScene.showRandomTrack(replay);
      },
      onclickHeatmap() {
        if (PublishedScene.heatmapShowing() ) {
          PublishedScene.hideHeatmap();
        } else {
          PublishedScene.showHeatmap();
        }
      }
    }
  }
</script>

<style scoped>
  .toggleNavBtn {
    border-radius: 5px;
    border: solid 2px #35a610;
    font-size: 26px;
    text-align: center;
    cursor: pointer;
  }
  .navBtnSelected {
    color: white;
    background-color: #33333394;
  }
</style>
