<template>
  <div class="gadgetBaseCircle ">
    <div id="compassBox" class="compass">
      <img src="../../../assets/img/gadget/compass3.svg" class="compassImg" @click="resetRotation">
    </div>
  </div>

</template>

<script>
  import PublishedScene from "../../../3d/js/PublishedScene";

  export default {
    name: "CompassItem",
    mounted() {
      let _this = this;
      let scene = PublishedScene.scene();
      scene.addEventListener('camera change', rotateCompass);

      function rotateCompass(event) {
        // console.log('rotateCompass')
        let deg = event.rotate * 180 / Math.PI;
        _this.compassDom.style.transform = 'rotate(' + deg + 'deg)';
      }
    },
    computed: {
      compassDom() {
        return document.getElementById('compassBox');
      }
    },
    methods: {
      resetRotation() {
        this.compassDom.style.transform = 'rotate(0deg)';
        PublishedScene.startResetCameraRotation();
      }
    }
  }
</script>

<style scoped>
.compass {
  width: 36px;
  height: 36px;
}
  .compassImg {
    width: 38px;
    height: 38px;
  }
</style>
