<template>
  <div>
    <div id="WebGL-output">
    </div>
  </div>
</template>

<script>
  import PublishedScene from "./js/PublishedScene";
  import Config from "./js/configurable/Config";
  import Common from "./js/util/Common";
  import {Vector3} from 'three';

  export default {
    name: "Main3d",
    mounted() {
      PublishedScene.init(this.$route.query.fromEditor);
      this.loadFromRemote();
    },
    methods: {
      loadFromRemote() {
        let fromRemote = Config.remote.previewMapId != null;
        if (!fromRemote)
          return;

        let _this = this;
        this.axios({
          url: '/merchant/merchantMap/download',
          method: 'post',
          responseType:'blob',
          data: {
            mapId: Config.remote.previewMapId,
            version: Config.remote.version,
          }
        }).then((response) => {
          console.log(response.data);
          if (response.data) {
            let mapContent = response.data; //unzip(response.data)
            _this.parseRemoteMap(mapContent);
          }
        }).catch(error => {
          console.log(error);
        });
      },
      parseRemoteMap(mapContent) {
        let _this = this;
        const resultJson = mapContent.slice(0, 1024);
        const reader = new FileReader();
        reader.readAsText(resultJson);
        reader.onload = function (event) {
          const content = Common.clearNull(reader.result);
          const headerObj = JSON.parse(content);
          // console.log("parseRemoteMap, headerObj: ", headerObj);
          for (let i = 0; i < headerObj.file.length; i++) {
            const mapPartObj = headerObj.file[i];
            const start = mapPartObj.start - 1;
            const end = mapPartObj.end;
            // console.log("parseRemoteMap, part: " + i, mapPartObj);
            if (i > 0) {
              continue;
            }
            const mapPartStr = mapContent.slice(start, end);
            const readerTmp = new FileReader();
            readerTmp.readAsText(mapPartStr);
            readerTmp.onload = function(event) {
              PublishedScene.deserialize(readerTmp.result);
              // PublishedScene.showHeatmap([{x: "-5.790", y: "12.680", value: 31}]);
              if (Config.remote.heatmap != undefined)
                _this.loadHeatmap();
              if (Config.remote.track != undefined)
                _this.loadUserTrack();
            }
          }
        }
      },
      loadHeatmap() {
        let payload = {encKey: Config.remote.encKey, mapId: Config.remote.mapId,
          beginTime:Config.remote.timeStart, endTime:Config.remote.timeEnd}
        this.axios({
          url: '/merchant/statistics/location/thermodynamicDiagrams',
          method: 'post',
          data: payload
        }).then((response) => {
          console.log(response);
          PublishedScene.showHeatmap(response.data.tdata);
        }).catch(error => {
          console.log(error);
        });
      },
      loadUserTrack() {
        let payload = {encKey: Config.remote.encKey, mapId: Config.remote.mapId, userId: Config.remote.userId,
          beginTime:Config.remote.timeStart, endTime:Config.remote.timeEnd}
        this.axios({
          url: '/merchant/statistics/location/trajectory',
          method: 'post',
          data: payload
        }).then((response) => {
          console.log("get user's track data");
          if (response.data.tdata && response.data.tdata.length > 0) {
            let positions = [];
            response.data.tdata.forEach(it => {
              positions.push(new Vector3(it.relativeX*1, 0.1, it.relativeY*-1));
            });
            PublishedScene.showRandomTrack(false, positions);
          }
        }).catch(error => {
          console.log(error);
        });
      },
    }
  }
</script>

<style scoped>
#WebGL-output {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
}

</style>
