<template>
  <div class="bbx" :class="{show: $store.state.showBottomInfo}">
    <div class="detail-box">
      <div class="puller">
        <div></div>
      </div>
      <div class="detail">
        <div class="building-name">仁和天地2号门</div>
        <div class="address-info">距您8KM  |  华府大道一段996号</div>
        <div class="pic-list">
          <div class="pic-item">
            <img src="../../../assets/img/mobile/sample/a.png">
          </div>
          <div class="pic-item">
            <img src="../../../assets/img/mobile/sample/b.png">
          </div>
          <div class="pic-item">
            <img src="../../../assets/img/mobile/sample/c.png">
          </div>
        </div>
        <div class="label-div">
          <div class="detail-label">详细信息</div>
          <div class="r90"><i class="iconfont iconxiala1"></i></div>
        </div>
        <div class="property">
          <div class="property-name">占地面积</div>
          <span class="property-value">1523㎡</span>
        </div>
        <div>
          <div class="property-name">绿化率</div>
          <span class="property-value">30%</span>
        </div>
      </div>
    </div>

    <div class="bottom-bar">
      <div class="bottom-bar-content">
        <img src="../../../assets/img/mobile/AddToFavorite.png" class="img-favorite" >
        <div class="txt-favorite div-txt">收藏</div>
        <img src="../../../assets/img/mobile/Share.png" class="img-share" >
        <div class="txt-share div-txt">分享</div>

        <div class="btn-like btn-pos" :class="{'btn-selected': btnActive}" @click="clickGoHere">
          <div>{{btnText()}}</div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
  import PublishedScene from "../../../3d/js/PublishedScene";
  import * as M from "../../../store/mutations-types";

  export default {
    name: "BuildingBottom",
    data() {
      return {
        addrName: "",
        btnActive: false
      }
    },
    mounted() {
      let detailBox = document.querySelector(".detail-box");
      let opacityEvent = {type: "top opacity change", value: 1};
      let expanded = false;
      detailBox.addEventListener("touchstart", (event) => {
        expanded = !expanded;
        if (expanded) {
          detailBox.style.setProperty('max-height', detailBox.scrollHeight + "px");
          opacityEvent.value = 0;
        } else {
          detailBox.style.setProperty('max-height', "164px");
          opacityEvent.value = 1;
        }
        event.stopImmediatePropagation();
        PublishedScene.scene().dispatchEvent(opacityEvent);
      });

      PublishedScene.scene().addEventListener("building selected", event => {
        if (event.modelName.trim().length > 0) {
          this.addrName = event.modelName;
          this.$store.commit(M.Show_BottomInfo, true);
          PublishedScene.navigatorTool().setDistination();
          if (this.btnActive)
            PublishedScene.navigatorTool().onPathFinding();
        } else {
          detailBox.style.setProperty('max-height', "164px");
          this.$store.commit(M.Show_BottomInfo, false);

          opacityEvent.value = 1;
          PublishedScene.scene().dispatchEvent(opacityEvent);
        }
      });
    },
    methods: {
      clickGoHere() {
        if (this.btnActive) {
          let detailBox = document.querySelector(".detail-box");
          detailBox.style.setProperty('max-height', "164px");
          //进入导航模式
          PublishedScene.navigatorTool().toggleNavigatingSwitch(true);
          this.btnActive = false;
          this.$store.commit(M.Is_Navigating, true);
          this.$store.commit(M.Show_BottomInfo, false);
        } else {
          // PublishedScene.navigatorTool().setDistination();
          this.btnActive = true;
          PublishedScene.navigatorTool().onPathFinding();
        }
      },
      btnText() {
        if (this.btnActive) {
          return "开始导航";
        }
        return "查看路线";
      }
    }
  }
</script>

<style scoped>
  .bbx {
    position: absolute;
    bottom: -310px;
    left: 0;
    right: 0;

    font-family:Source Han Sans SC;
    transition: 0.3s;
    visibility: hidden;
  }

  .show {
    visibility: visible;
    bottom: 0px;
  }
  .puller {
    height: 26px;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .puller>div {
    width:161px;
    height:9px;
    background:rgba(208,201,214,1);
    border-radius:5px;
  }
  .detail-box {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 130px;
    z-index: 2;
    max-height: 164px;
    transition: 0.3s;
  }
  .detail {
    padding: 20px 45px;
    background-color: white;
    box-shadow:-1px -3px 34px 4px rgba(140,140,140,0.15);
    border-radius:20px 20px 0 0;
  }
  .building-name {
    font-size:30px;
    font-weight:bold;
    color: #333;
    line-height: 2;
  }
  .address-info {
    font-size:22px;
    font-weight:bold;
    color: #333;
    line-height: 2;
  }
  .pic-list {
    display: flex;
    align-items: center;
    height: 225px;
    width: 100%;
    /*border: solid 1px forestgreen;*/
  }

  .pic-item {
    width: 205px;
    height: 198px;
  }
  .pic-item>img {
    width: 188px;
    height: 100%;
    object-fit: cover;
  }
  .detail-label {
    font-size:24px;
    font-weight:500;
    color: #333;
  }
  .r90 {
    transform:rotate(-90deg);
    font-size: 32px;
    margin-right: 30px;
  }
  .label-div {
    display: flex;
    justify-content: space-between;
    margin: 20px 0 5px
  }
  .property {
    line-height: 2;
  }
  .property-name {
    display: inline-block;
    width: 152px;
    color: #666;
    font-size: 22px;
    font-weight:400;
  }
  .property-value {
    color: #333;
    font-size: 22px;
    font-weight:400;
  }

.bottom-bar {
  background-color: white;
  z-index: 3;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 131px;
}
.bottom-bar-content {
  border-top: solid 1px #E5E5E5;
  margin: 0 48px 20px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
  .img-favorite {
    position: absolute;
    left: 5px;
    top: 27px;
  }
  .img-share {
    position: absolute;
    left: 78px;
    top: 27px;
  }
  .div-txt {
    padding: 0;
    margin: 0;
    font-weight:400;
    font-size: 24px;
    color: #666;
  }
  .txt-favorite {
    position: absolute;
    left: -3px;
    top: 64px;
  }
  .txt-share {
    position: absolute;
    left: 70px;
    top: 64px;
  }
  .btn-like {
    border:2px solid #6979F8;
    border-radius:8px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #6979F8;
    font-size:24px;
    font-weight:bold;
    padding: 17px 43px 19px;
  }
  .btn-selected {
    background-color: #6979F8;
    color: white;
  }
  .btn-pos {
    position: absolute;
    bottom: 21px;
    right: 0;
  }
</style>
