<template>
  <div class="bottom-box" :class="{show: $store.state.showBottomBox}">
    <div class="border arrow-box">
      <div class="iconfont iconxiala1 arrow" @mousedown="clickBackBtn"></div>
    </div>
    <div class="border bottom-content">
      <div class="border input-area">
        <div class="border inputs">
          <div class="border input-row-area">
            <div class="dot blue"/>
            <div class="under-line">
              <input type="text" class="input-address" :value="srcPosName">
            </div>
          </div>
          <div class="border input-row-area">
            <div class="dot red"/>
            <div class="under-line">
              <input type="text" class="input-address" :value="dstPosName">
            </div>
          </div>
        </div>
        <div class="border switch-area">
          <i class="iconfont iconqiehuan" style="font-size: 50px" @mousedown="clickSwitchBtn"/>
        </div>
      </div>
      <div class="border btns-area">
        <i class="iconfont iconbuhang blue-font"/>
        <span style="padding-left: 10px">0米</span>
        <i class="iconfont iconshijian blue-font" style="padding-left: 10px"/>
        <span style="padding-left: 10px">0分钟</span>
        <div style="flex: 1;"/>
        <div class="btn-like" @mousedown="simulateNavigate">
          <i class="iconfont iconweizhi btn-like-txt" />
          <div class="btn-txt">模拟导航</div>
        </div>
        <div class="btn-like">
          <i class="iconfont iconweizhi btn-like-txt"/>
          <div class="btn-txt">立即导航</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {Show_BottomBox} from "../../../store/mutations-types";
  import PublishedScene from "../../../3d/js/PublishedScene";

  export default {
    name: "BottomBox",
    mounted() {
      PublishedScene.scene().addEventListener("map pos select", event => {
        this.srcPosName = event.start;
        this.dstPosName = event.end;
      });
    },
    data() {
      return {
        srcPosName: "起点",
        dstPosName: ""
      }
    },
    methods: {
      clickSwitchBtn() {
        // console.log("clickSwitchBtn");
        let temp = this.srcPosName;
        this.srcPosName = this.dstPosName;
        this.dstPosName = temp;
        PublishedScene.navigatorTool().switchMarker();
      },
      clickBackBtn() {
        // console.log("clickBackBtn");
        this.$store.commit(Show_BottomBox, false);
        PublishedScene.navigatorTool().startSetEndPosition(false);
        PublishedScene.navigatorTool().clearPathAnimation();
      },
      simulateNavigate() {
        PublishedScene.navigatorTool().onPathFinding();
      }
    }
  }
</script>

<style scoped>
  .btn-txt {
    padding-left: 10px;
  }
.bottom-box {
  position: fixed;
  bottom: -390px;
  left: 30px;
  right: 30px;
  height: 300px;
  background-color: white;
  box-shadow:0px 1px 6px 0px rgba(0, 0, 0, 0.2);
  border-radius:20px;
  display: flex;
  transition: 0.5s;
}
.show {
  bottom: 30px;
}
  .border {
    /*border: solid 2px #63a35c;*/
  }
  .bottom-content {
    flex: 1;
    height: 300px;
    flex-direction: column;
  }
  .arrow-box {
    width: 80px;
    height: 300px;
    text-align: center;
    color: black;
  }
  .arrow {
    transform: rotate(90deg);
    font-size: 42px;
    margin-top: 30px;
  }
  .input-area {
    height: 60%;
    width: 100%;
    display: flex
  }
  .inputs {
    flex: 1;
    flex-direction: column;
    height: 100%;
    /*padding-top: 25px;*/
  }
  .switch-area {
    width: 120px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 36px;
  }
  .btns-area {
    padding-top: 17px;
    display: flex;
    align-items: center;
    color: black;
    font-size: 35px;
    transition: 1s;
  }
  .btn-like {
    border:1px solid rgba(235,235,235,1);
    box-shadow:0px 1px 6px 0px rgba(0, 0, 0, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 40px;
    padding: 20px;
    margin-right: 20px;
  }
  .btn-like-txt {
    color: #2381F6;
    font-size: 35px;
  }
  .input-row-area {
    width: 100%;
    height: 50%;
    display: flex;
    align-items: center;
  }
  .dot {
    width: 24px;
    height: 24px;
    margin-right: 30px;
    border-radius: 50%;
  }
  .blue-font {
    font-size: 50px;
    color: rgb(35, 129, 246);
  }
  .blue {
    background-color: #2381F6;
  }
  .red {
    background-color: #E93935;
  }
  .input-address {
    font-size: 36px;
    border: none;
    outline: none;
  }
  .under-line {
    flex: 1;
    border-bottom: solid 1px #d1d1d1;
    padding: 20px 0;
  }
</style>
